<template>
  <div>
    <el-row>
      <el-col>
        <el-table :data="tableData" stripe style="width: 100%" max-height="550px" border>
          <el-table-column label="操作系统" header-align="center" align="center">
            <template
              slot-scope="scope"
            >{{scope.row.server_type==1?'JAVA':''}}{{scope.row.server_type==2?'python':''}}{{scope.row.server_type==3?'node':''}}</template>
          </el-table-column>
          <el-table-column prop="user_name" label="操作人" header-align="center" align="center"></el-table-column>
          <el-table-column prop="user_ip" label="ip地址" header-align="center" align="center"></el-table-column>
          <el-table-column label="操作时间" header-align="center" align="center">
            <template slot-scope="scope">{{scope.row.create | formatDate}}</template>
          </el-table-column>
          <el-table-column prop="user_city" label="地区" header-align="center" align="center"></el-table-column>
          <el-table-column prop="user_post" label="邮编" header-align="center" align="center"></el-table-column>
          <el-table-column width="300" prop="log_msg" label="操作内容" header-align="center" align="center"></el-table-column>
        </el-table>
      </el-col>

      <el-col class="mt10 df jcfe">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="current_page"
          :page-sizes="[50, 100, 200]"
          :page-size="page_size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          background
        ></el-pagination>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      current_page: 1, //当前选择的第几页
      total: 1, //总共有多少条数据
      page_size: 50, //当前一页选择的条数
      filters_type: [],
      tableData: []
    };
  },
  mounted() {
    this.get_log_list();
  },
  methods: {
    handleCurrentChange(res) {
      this.current_page = res;
      this.get_log_list();
    },
    handleSizeChange(res) {
      this.page_size = res;
      this.get_log_list();
    },
    get_log_list() {
      this.$post("log/getlog", {
        page: this.current_page,
        paging: this.page_size
      }).then(res => {
        this.tableData = res.data;
        this.total = res.total;
      });
    }
  }
};
</script>

<style>
</style>


